:root {
    --bg: #fdfdfd;
    --highlight1: #ed4264;
    --highlight2: #ffedbc;
    --color: #1a1e24;
    --font-number: Montserrat, Roboto, Helvetica, Arial, sans-serif;
    --font-head: "Space Mono", Consolas, Menlo, Monaco, "Courier New", monospace;
    --font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  }
  .leaderboard__name {
    color: #979cb0;
    font-weight: 600;
    font-size: 20px;
    letter-spacing: 0.64px;
    margin-left: 12px;
  }
  .leaderboard__value {
    color: #35d8ac;
    font-weight: 700;
    font-size: 28px;
    text-align: right;
  }
  .leaderboard__value > span {
    opacity: 0.8;
    font-weight: 600;
    font-size: 25px;
    margin-left: 3px;
  }
  .leaderboard__normalstate, .leaderboard__drawstate{
    display: grid;
  grid-template-columns: 0.5fr 3fr 2fr;
  align-items: center;
  padding: 10px 30px 10px 10px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
  cursor: pointer;
  transition: transform 0.25s cubic-bezier(0.7, 0.98, 0.86, 0.98),
    box-shadow 0.25s cubic-bezier(0.7, 0.98, 0.86, 0.98);
  background-color: #fff;
  }
  .leaderboard__normalstate:hover {
    transform: scale(1.2);
    box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
  }
  .position {
    font: bold 2.25rem/1 var(--font-number);
    width: 4rem;
    height: 4rem;
    float: left;
    margin: 0;
    color: var(--bg);
    background: var(--highlight1)
      linear-gradient(to bottom right, var(--highlight1) 25%, var(--highlight2));
    text-shadow: 0 0 2px var(--highlight1);
    border-radius: 50%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    shape-outside: ellipse();
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  .btn-edit{
    width: 50px;
    border: none;
    outline: none;
    background: transparent;
    color: orange;
  }
  .btn-delete{
    width: 50px;
    border: none;
    outline: none;
    background: transparent;
    color: rgb(235,15,15);
  }